* {
	color: black;
}

body, html {
	background-color: transparent;
	
	
	

    
}

#pdf {
	/* 96 ppi */
	height: 100%;
	width: 100%;
	background-color: white;
    padding: 100px;
	font-family: 'Quicksand', sans-serif;
}

#header {
	display: flex;
}

#header-left {
	width: 25%;
	padding: 10px;
}

#header-middle {
	display: flex;
	width: 50%;
	/* margin: auto;
	text-align: center; */
}

#header-middle p {
	margin: auto;
	font-size: 45px;
	font-weight: bold;
}

#header-right {
	width: 25%;
	padding: 10px;
	text-align: right;
}

a {
	text-decoration: none;
	color: black;
}

.section {
	padding: 5px 10px 0px 10px;
	padding-bottom: 0px;
}

.section-header {
	border-bottom: 2px solid black;
	font-size: 20px;
	font-weight: bold;
}

.item-header {
	display: flex;
	padding-top: 5px;
}

.item-title {
	color: black;
	width: 80%;
  
    
}

.item-date {
	width: 20%;
}

.item ul {
	/* padding: 0; */
	padding-left: 20px;
	margin: 0;
}

.fa-code{
    font-size: x-large;
}


@media screen and (max-width: 480px) {
    .p {
      width: 40%;
      height: 20vh;
    }

	.title-name {
		width: 10px;
	}
  }
